.size__list {
  @include list-reset;
  margin-top: 30px;
  margin-bottom: 30px;
  display: flex;
  justify-content: space-between;

  @media (min-width: $screen-tablet) {
    margin-top: 0;
    margin-bottom: 0;
    padding-bottom: 31px;
  }
}

.size__item {
  display: block;
}

.size__label {
  display: block;
  padding: 20px 35px;
  text-transform: uppercase;
  border: 2px $border-type rgba($color-font, 0.1);

  &:hover,
  &:focus {
    border-color: rgba($color-font, 0.3);
  }

  &:active {
    border-color: $color-primary--light;
  }

  @media (min-width: $screen-tablet) {
    @include font-set(17px, 16px, bold);
    padding: 16px 48px;
  }
}

.size__input:checked + .size__label {
  border-color: $color-primary--light;
}
