.site-list {
  @include font-set(17px, 24px, bold);
  @include list-reset;
  display: flex;
  flex-direction: column;

  @media (min-width: $screen-tablet) {
    @include wrapper;
    flex-direction: row;
    flex-wrap: wrap;
    width: $screen-tablet;
  }

  @media (min-width: $screen-desktop) {
    width: $screen-desktop;
  }
}

.site-list__link {
  display: block;
  padding-top: 27px;
  padding-bottom: 29px;
  color: $black;
  transition: all 0.2s ease;

  @media (min-width: $screen-tablet) {
    padding-top: 29px;
    padding-bottom: 28px;
  }

  @media (min-width: $screen-desktop) {
    padding-top: 18px;
  }

  &:hover,
  &:focus {
    color: rgba($black, 0.6);
    outline: none;
  }

  &:active {
    color: rgba($black, 0.3);
  }
}

.site-list__link:hover .site-list__icon,
.site-list__link:focus .site-list__icon {
  fill: $color-primary;
  opacity: 1;
}

.site-list__link:active .site-list__icon {
  opacity: 0.3;
}

.site-list__link--logo {
  padding-top: 22px;
  padding-bottom: 18px;

  @media (min-width: $screen-desktop) {
    padding-bottom: 0;
    padding-top: 9px;
    padding-right: 20px;

    &:hover,
    &:focus {
      opacity: 0.6;
    }

    &:active {
      opacity: 0.3;
    }
  }
}

.site-list__link--search {
  @media (min-width: $screen-tablet) {
    min-height: 80px;
  }
}


.site-list__link--catalog {
  @media (min-width: $screen-tablet) {
    padding-top: 14px;
  }

  @media (min-width: $screen-desktop) {
    padding-top: 30px;
  }
}

.site-list__link--order {
  @media (min-width: $screen-tablet) {
    padding-top: 14px;
  }

  @media (min-width: $screen-desktop) {
    padding-top: 30px;
    text-align: right;
  }
}

.site-list__link--cart {
  @media (min-width: $screen-desktop) {
    padding-top: 28px;
  }
}

.site-list__item {
  position: relative;
  padding-left: 76px;
  padding-right: 76px;
  box-shadow: 0 1px 0 0 $grey--light, inset 0 1px 0 0 $grey--light;

  &--catalog,
  &--order,
  &--search,
  &--cart {
    display: none;
  }

  @media (min-width: $screen-tablet) {
    @include font-set(15px, 24px, bold);
    display: block;
    box-shadow: none;
    padding: 0;
  }
}

.site-list__item--logo {
  display: block;
  text-align: center;

  @media (min-width: $screen-tablet) {
    order: 1;
    width: 320px;
    text-align: left;
  }

  @media (min-width: $screen-desktop) {
    order: 3;
    text-align: center;
  }
}

.site-list__item--search {
  @media (min-width: $screen-tablet) {
    font-size: 0;
    width: 84px;
    order: 2;
    border-left: $border;
    border-right: $border;
    margin-left: auto;
    margin-bottom: 3px;
  }

  @media (min-width: $screen-desktop) {
    order: 4;
    margin-bottom: 0;
  }
}

.site-list__item--cart {
  padding-right: 60px;

  @media (min-width: $screen-tablet) {
    order: 3;
    font-size: 17px;
    width: 264px;
    text-align: right;
    padding-left: 60px;
    padding-right: 0;
  }

  @media (min-width: $screen-desktop) {
    order: 5;
    width: 266px;
  }
}

.site-list__item--catalog {
  @media (min-width: $screen-tablet) {
    order: 4;
    width: 168px;
  }

  @media (min-width: $screen-desktop) {
    @include font-set(17px, 24px, bold);
    order: 1;
    width: 183px;
    border-right: $border;
  }
}

.site-list__item--order {
  @media (min-width: $screen-tablet) {
    order: 5;
    width: 168px;
  }

  @media (min-width: $screen-desktop) {
    @include font-set(17px, 24px, bold);
    order: 2;
    width: 191px;
  }
}

.site-list__item--new {
  display: none;

  @media (min-width: $screen-desktop) {
    display: block;
    order: 6;
    margin-right: 60px;
  }
}

.site-list__item--sale {
  display: none;

  @media (min-width: $screen-desktop) {
    display: block;
    order: 7;
  }
}

.main-nav--opened,
.main-nav--nojs {
  .site-list__item {
    &--catalog,
    &--order,
    &--search,
    &--cart {
      display: block;
    }
  }
}

.site-list__logo-img {
  @media (min-width: $screen-tablet) {
    width: 113px;
    height: 46px;
  }

  @media (min-width: $screen-desktop) {
    width: 149px;
    height: 72px;
  }
}

.site-list__icon {
  position: absolute;
  content: "";
  top: 50%;
  left: 30px;
  margin-top: -9px;
  max-width: 21px;
  max-height: 21px;
  opacity: 0.2;
  transition: color 0.2s ease;

  &--cart {
    width: 20px;
    height: 20px;
  }
}
